import fs from "fs";
import path from "path";
import BlogPostClient from "././BlogPostClient";
import { remark } from "remark";
import html from "remark-html";

type BlogPageProps = {
  params: { slug: string };
};

export default async function BlogPage({ params }: BlogPageProps) {
  const { slug } = params;

  // Path to Markdown file
  const filePath = path.join(process.cwd(), "src", "app", "blog", "data", `${slug}.md`);

  // Read Markdown content
  let content = "";
  try {
    content = fs.readFileSync(filePath, "utf-8");
  } catch (err) {
    content = "# Content Not Found\nThe requested blog content does not exist.";
  }

  // Convert Markdown to HTML
  const processedContent = await remark().use(html).process(content);
  const contentHtml = processedContent.toString();

  // Use slug as title (replace hyphens with spaces, capitalize words)
  const title = slug
    .replace(/-/g, " ")
    .replace(/\b\w/g, (c) => c.toUpperCase());

  return <BlogPostClient post={{ title, content: contentHtml }} />;
}

/** Static export support */
export async function generateStaticParams() {
  const dataDir = path.join(process.cwd(), "src", "app", "blog", "data");
  const files = fs.readdirSync(dataDir);
  return files.map((file) => ({
    slug: file.replace(".md", ""),
  }));
}
